<template>
  <div class="type-container">
    <p class="desc"></p>
    <div class="list-group">
      <div class="list-item" v-for="ap in appointmentInfo">
        <span class="name">{{ap.name}}</span>
        <span class="count">{{ap.count}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "appointment",
    data() {
      return {
        appointmentInfo: [
          {
            name: '',
            count: '2',
          },
          {
            name: '',
            count: '0',
          },
          {
            name: '',
            count: '6',
          },
          {
            name: '',
            count: '1',
          },
        ]
      }
    }
  }
</script>

<style lang="less" scoped>
  .type-container {
    .desc {
      color: #333333;
      font-size: 13px;
      line-height: 18px;
      margin-bottom: 15px;
    }
    .list-title {
      color: #333333;
      font-size: 13px;
      line-height: 43px;
    }
    .list-group {
      .list-item {
        height: 45px;
        border-bottom: 1px solid #DBDBDB;
        span {
          line-height: 45px;
          font-size: 13px;
          &.count {
            float: right;
          }
        }
        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
</style>



