import Vue from 'vue'
import Router from 'vue-router'

/* add all component */
import MenuSelect from '@/pages/MenuSelect'
import Index from '@/pages/menu-select/Index'
import Study from '@/pages/menu-select/Study'
import Find from '@/pages/menu-select/Find'
import Home from '@/pages/menu-select/Home'
import CreditDetail from '@/pages/CreditDetail'
import CreditUp from '@/pages/CreditUp'
import CreditKnow from '@/pages/CreditKnow'
import Identity from '@/pages/credit-type/Identity'
import LawAbiding from '@/pages/credit-type/LawAbiding'
import Appointment from '@/pages/credit-type/Appointment'
import Learn from '@/pages/credit-type/Learn'
import Conduct from '@/pages/credit-type/Conduct'
import EndorsementsDetail from '@/pages/EndorsementsDetail'
import EndorsementsEmpty from '@/pages/EndorsementsEmpty'
import EndorsementsMore from '@/pages/EndorsementsMore'
import EditCar from '@/pages/EditCar'
import _404 from '@/pages/404'

Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}

Router.prototype.backTo = function($router, path) {
  this.isBack = true
  $router.push(path)
}

Router.prototype.goBackLength = function(length = 1){
  this.isBack = true
  window.history.go(-length)
}

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: '', redirect: '/index' },
    { path: '/menuselect', name: 'Menu', component: MenuSelect, children: [
        { path: '/index', name: '', component: Index },
        { path: '/study', name: '', component: Study },
        { path: '/find', name: '', component: Find },
        { path: '/home', name: '', component: Home }
      ]
    },
    { path: '/creditDetail', name: '', component: CreditDetail},
    { path: '/creditKnow', name: '', redirect: './creditKnow/identity', component: CreditKnow, children: [
        { path: '/creditKnow/identity', name: '', component: Identity },
        { path: '/creditKnow/lawAbiding', name: '', component: LawAbiding },
        { path: '/creditKnow/appointment', name: '', component: Appointment },
        { path: '/creditKnow/learn', name: '', component: Learn },
        { path: '/creditKnow/conduct', name: '', component: Conduct }
      ]
    },
    { path: '/creditUp', name: '', component: CreditUp},
    { path: '/endorsement_detail', name: '', component: EndorsementsDetail},
    { path: '/endorsement_empty', name: '()', component: EndorsementsEmpty},
    { path: '/endorsement_more', name: '', component: EndorsementsMore},
    { path: '/edit_car', name: '', component: EditCar},
    { path: '/404', name: '404', component: _404},
    { path: '*', redirect: '/404' }
  ]
})




